body {
  margin: 0;
  background-color: #33658A;
  text-align: center;
}

.header {
  overflow: hidden;
  padding: 1em 0.625em;
  text-align: center;
  max-height: 4em;
  background: linear-gradient(to right, #241d43, #214747); /* Initial gradient background */
  background-size: 200% 100%; /* Initial size of the background gradient */
  animation: headerFade 10s infinite alternate;
}

@keyframes headerFade {
  0% {
    background-position: 0 0; /* Start position of the gradient background */
  }
  100% {
    background-position: 100% 0; /* End position of the gradient background */
  }
}



/* Style the header links */
.header a {
  float: left;
  color: rgba(255, 255, 255, 0.659);
  text-align: center;
  padding: 0.75em;
  text-decoration: none;
  font-size: 1.125em;
  line-height: 1.5625em;
  border-radius: 4px;
}

.header a.logo .logo-container {
  display: inline-block;
  padding: 0em;
}

/* Change the background color on mouse-over */
.header a:hover {
  background-color: #ddd;
  color: black;
}

/* Float the link section to the right */
.header-right {
  float: right;
}

.body-content {

  padding: 1.25em;
}

.body-content .body-image {
  max-width: 25%;
  height: auto;
  display: block;
  margin: 0 auto;
  margin-top: 1em;
  float: left;
}

.body-content .body-text-box {
  background: rgb(237, 204, 204);
  width: 80%;
  max-width: 600px;
  height: 80%;
  max-height: 600px;
  padding: 20px;
  border-radius: 50px;
  box-shadow: 0 0 400px rgba(231, 230, 157, 0.5), 0 0 60px rgba(225, 223, 114, 0.5);
  margin-top: 2em;
  margin-left: auto;
  margin-right: auto;
  transition: box-shadow 0.3s ease-in-out;
  border: 8px solid rgb(128, 0, 128);
  text-shadow: 3px 3px 4px rgba(0, 0, 0, 0.2);
  text-align: inherit;
  font-size: 1.5em;
  float: right
}

.body-content .body-text-box:hover {
  box-shadow: 0 0 40px rgba(77, 0, 153, 0.8), 0 0 60px rgba(204, 0, 255, 0.8); /* Adjust shadow on hover */
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  padding: 20px;
  background-color: hsl(226, 78%, 75%);
}

.footer-images {
  float: right;
}

.footer-images img {
  padding-right: 40px;
}





/* Add media queries for responsiveness - when the screen is 500px wide or less, stack the links on top of each other */
@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  .header-right {
    float: none;
  }
}
